<template>
  <div>
    <div 
      v-if="isMobile && isMobileMenuVisible" 
      class="mobile-overlay"
      @click="toggleMenu"
    ></div>

    <div class="mobile-toggle" :class="{ 'mobile-toggle-dark': isDarkTheme }" @click="toggleMenu">
      <el-icon size="24">
        <Expand v-if="isMobile ? !isMobileMenuVisible : isCollapsed" />
        <Fold v-else />
      </el-icon>
    </div>
    
    <div class="side-navigation" 
      :class="{
        'side-navigation-dark': isDarkTheme,
        'mobile-visible': isMobileMenuVisible,
        'collapsed': !isMobile && isCollapsed
      }">

      <el-button class="collapse-btn" @click="toggleCollapse" v-if="!isMobile">
        <el-icon>
          <Fold v-if="!isCollapsed" />
          <Expand v-else />
        </el-icon>
      </el-button>

      <div class="unified-control-panel">
        
        <el-button 
          class="scroll-bottom-btn"
          @click="scrollToBottom"
          title=""
          v-show="!isCollapsed"
        >
          <el-icon><ArrowDownBold /></el-icon>
        </el-button>

        <el-input
          v-model="searchText"
          placeholder="..."
          clearable
          prefix-icon="Search"
          @input="handleSearchInput"
          v-show="!isCollapsed"
        />

        <div :class="['statistics-info', { collapsed: isCollapsed }]"> 
          <span>: {{ filteredCategories.length }} </span>
          <span> {{ totalLinksCount }} </span>
        </div>
      </div>

      <div class="side-nav-content" ref="scrollContainer">
        <el-menu
          :default-active="activeIndex"
          class="side-nav-menu"
          ref="sideMenu"
          @select="handleSelect"
          :collapse="isCollapsed" 
          :collapse-transition="false"
        >
          <div class="groups-container">
            <div 
              v-for="(groupItems, groupName) in groupedCategories" 
              :key="groupName"
              class="category-group-box"
            >
              <div v-if="!isCollapsed && groupName !== 'default_ungrouped'" class="group-title">
                {{ groupName }}
              </div>

              <div class="menu-grid" :class="{ 'menu-grid-collapsed': isCollapsed }">
                <el-menu-item 
                  v-for="category in groupItems" 
                  :key="category.name" 
                  :index="category.name"
                  :class="{ 'is-active': activeIndex === category.name }"
                >
                  <span v-if="category.emoji" class="emoji-icon">{{ category.emoji }}</span>
                  <el-icon v-else>
                    <component :is="category.icon || 'Folder'" />
                  </el-icon>
                  
                  <template #title>
                    <span class="category-name">{{ category.name }}</span>
                    <span class="item-count">{{ getCategoryCount(category) }}</span>
                  </template>
                </el-menu-item>
              </div>
            </div>


            <div v-if="filteredCategories.length === 0" class="empty-state">
              <el-icon size="20" color="#909399"><WarnTriangleFilled /></el-icon>
              <p></p>
              <el-button link type="primary" @click="searchText = ''; handleSearchInput()"></el-button>
            </div>

          </div>
        </el-menu>
      </div>

      <div class="back-to-top-wrapper">
        <el-button 
          class="back-to-top-btn"
          @click="scrollToTop"
          title=""
        >
          <el-icon><ArrowUpBold /></el-icon>
        </el-button>
      </div>
    </div>
  </div>
</template>

<script>
import { Folder, Expand, Fold, Monitor, Search, ArrowUpBold, ArrowDownBold } from '@element-plus/icons-vue'
import { navData } from "../nav-data.js"
import * as ElementPlusIcons from '@element-plus/icons-vue'
import { nextTick } from 'vue'
import { getPinyinInitials } from "../utils/pinyin.js"

export default {
  name: 'SideNavigation',
  components: {
    Folder, Expand, Fold, Monitor, Search, ArrowUpBold, ArrowDownBold,
    ...ElementPlusIcons
  },
  props: {
    isDarkTheme: { type: Boolean, default: false }
  },
  data() {
    return {
      activeIndex: '',
      categories: navData,
      isMobileMenuVisible: false,
      searchText: '',
      filteredCategories: navData, // 
      isMobile: window.innerWidth <= 768,
      isCollapsed: false,
      resizeTimer: null,
      searchTimer: null,
    }
  },
  computed: {
    totalLinksCount() {
      return this.filteredCategories.reduce((total, category) => {
        return total + this.getCategoryCount(category);
      }, 0);
    },
    //  Group 
    groupedCategories() {
      const groups = {};
      const otherCategory = [];
      
      this.filteredCategories.forEach(cat => {
        const groupName = cat.group || ''; 
        
        if (groupName === '') {
          otherCategory.push(cat);
        } else {
          if (!groups[groupName]) {
            groups[groupName] = [];
          }
          groups[groupName].push(cat);
        }
      });
      
      const result = {};
      
      Object.keys(groups).forEach(groupName => {
        if (groupName !== '') {
          result[groupName] = groups[groupName];
        }
      });
      
      if (otherCategory.length > 0) {
        result[''] = otherCategory;
      }

      return result;
    }
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.handleResize);
  },
  mounted() {
    this.handleResize();
    window.addEventListener('resize', this.handleResize);
  },
  methods: {
    handleResize() {
      if (this.resizeTimer) clearTimeout(this.resizeTimer);
      this.resizeTimer = setTimeout(() => {
        this.isMobile = window.innerWidth <= 768;
        if (!this.isMobile && this.isMobileMenuVisible) {
          this.isMobileMenuVisible = false;
        }
      }, 100);
    },

    getCategoryCount(category) {
      if (category.subCategories && category.subCategories.length > 0) {
        return category.subCategories.reduce((acc, sub) => acc + (sub.links ? sub.links.length : 0), 0);
      }
      if (category.links) {
        return category.links.length;
      }
      return 0;
    },

    handleSearchInput() {
      if (this.searchTimer) clearTimeout(this.searchTimer);
      this.searchTimer = setTimeout(() => {
        this.filterCategories();
      }, 300);
    },

    filterCategories() {
      if (!this.searchText) {
        this.filteredCategories = this.categories;
        return;
      }
      const searchLower = this.searchText.trim().toLowerCase();
      
      this.filteredCategories = this.categories.filter(category => {
        const categoryName = category.name.toLowerCase();
        const pinyinInitials = getPinyinInitials(category.name).toLowerCase();
        
        const matchName = categoryName.includes(searchLower) || pinyinInitials.includes(searchLower);
        
        const groupName = (category.group || '').toLowerCase();
        const matchGroup = groupName.includes(searchLower);

        let matchSub = false;
        if (category.subCategories) {
          matchSub = category.subCategories.some(sub => sub.name.toLowerCase().includes(searchLower));
        }

        return matchName || matchSub || matchGroup;
      });
    },

    handleSelect(index) {
      this.activeIndex = index;
      this.$emit('category-selected', index);
      this.$emit('clear-top-search');
      if (this.isMobile) {
        this.isMobileMenuVisible = false;
      }
    },
    toggleMenu() {
      if (this.isMobile) {
        this.isMobileMenuVisible = !this.isMobileMenuVisible;
        nextTick(() => { window.dispatchEvent(new Event('resize')); });
      } else {
        this.isCollapsed = !this.isCollapsed;
        this.$emit('update:isCollapsed', this.isCollapsed);
      }
    },

    scrollToTop() {
      const container = this.$refs.scrollContainer;
      if (container) {
        container.scrollTo({ top: 0, behavior: 'smooth' });
      }
    },

    scrollToBottom() {
      const container = this.$refs.scrollContainer;
      if (container) {
        container.scrollTo({ top: container.scrollHeight, behavior: 'smooth' });
      }
    },

    toggleCollapse() {
      this.isCollapsed = !this.isCollapsed;
      this.$emit('update:isCollapsed', this.isCollapsed);
    }
  }
}
</script>

